import { Component, OnInit } from '@angular/core';
//import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import _ from 'lodash';
import * as moment from 'moment';
import io from 'socket.io-client';
import { PostService } from 'src/app/services/post.service';
import { TokenService } from './../../services/token.service';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})
export class PostsComponent implements OnInit {
   socket: any;
  posts = [];
  user: any;
  //editForm: FormGroup;
  postValue: any;
  modalElement: any;

  private noOfItemsToShowInitially = 3;
  private itemsToLoad = 3;

  public itemsToShow: any;
  public isFullListDisplayed = false;

  constructor(private postService: PostService, private tokenService:TokenService, private router:Router) {
  this.socket=io('http://localhost:3000');
   }

   ngOnInit() {
   this.user=this.tokenService.GetPayload();
   this.AllPosts();
   this.socket.on('refreshPage', data => {
      this.AllPosts();
    });
   
   }


AllPosts() {
    this.postService.getAllPosts().subscribe(
      data => {
        this.posts = data.posts;
        this.itemsToShow = this.posts.slice(0, this.noOfItemsToShowInitially);
      },
      err => {
        if (err.error.token === null) {
          this.tokenService.DeleteToken();
          this.router.navigate(['']);
        }
      }
    );
  }

  LikePost(post) {
   this.postService.addLike(post).subscribe(data =>{
       console.log(data)
      this.socket.emit('refresh',{});

     }, err=> console.log(err));

   }
   

  CheckInLikesArray( arr, username){
return _.some(arr,{username:username});
 }
OpenCommentBox(post) {
    this.router.navigate(['post', post._id]);
  }


   TimeFromNow(time){
  return moment(time).fromNow();
  }



}
